.main-heading {
    text-align: center;
    margin-top: 30px;
    font-family: 'Montserrat', sans-serif;
}

 .price {
    color: hsl(232, 13%, 33%);
} 
 .para {
    margin-top: 30px;
    color: hsl(234, 14%, 74%);
    /* padding: 0px; */
} 

.switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 25px;
}  

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
}  

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    /* background-color: #ccc; */
    background-color: hsl(237, 63%, 64%);
    -webkit-transition: .4s;
    transition: .4s;
} 

 .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;

} 
 
input:checked+.slider {
    background-color: hsl(237, 63%, 64%);
} 
 
input:focus+.slider {
    box-shadow: 0 0 1px hsl(237, 63%, 64%);
} 
 
input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(19px);
} 

/* Rounded sliders */
 .slider.round {

    border-radius: 34px;
} 

 .slider.round:before {
    border-radius: 50%;
} 